@import url("https://fonts.googleapis.com/css2?family=Palette+Mosaic&display=swap");

.menu {
  padding-top: 20vh;
  display: grid;
  font-family: "Palette Mosaic", cursive;
  color: white;
}

.dulce {
  font-family: "Palette Mosaic", cursive;
  height: 250px;
  width: 250px;
  display: grid;
  justify-content: center;
  border-radius: 50px;
  animation: drop 1s ease;
}

.bunatati {
  background-color: rgb(14, 10, 10);
  display: grid;
  justify-content: center;
}

.produse {
  font-family: "Palette Mosaic", cursive;
  display: grid;
  justify-content: center;
  padding-bottom: 40px;
  text-shadow: 2px 3px 10px #be7506;
  color: rgb(218, 151, 27);
}

.name {
  padding-top: 30px;
  text-shadow: 2px 3px 10px #be7506;
  color: rgb(218, 151, 27);
  justify-content: center;
  text-align: center;
}

@keyframes drop {
  0% {
    opacity: 0;
    transform: translateY(-0px);
  }
  50% {
    opacity: 0.5;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
