@import url("https://fonts.googleapis.com/css2?family=Palette+Mosaic&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Titillium Web", sans-serif;
}

.video {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* z-index: 1; */
  filter: brightness(50%);
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.content {
  /* position: absolute; */
  z-index: 10;
  padding: 10px 0px 0px 10px;
  border-radius: 50%;
  display: flex;
  justify-content: space-between;
}

.title {
  position: absolute;
  z-index: 10;
  margin: 0;
  text-shadow: 2px 3px 10px #be7506;
  color: rgb(218, 151, 27);
  font-size: 3.5vh;
  top: 25%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  font-family: "Palette Mosaic", cursive;
}

.rulota {
  border-radius: 30px;
  width: 18vh;
  animation: run 1s;
  animation: run 1s linear, shake infinite 2s linear;
}

@keyframes run {
  from {
    margin-left: 200%;
    width: 50%;
  }
}
@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

@media (max-width: 700px) {
  .rulota {
    width: 16vh;
    animation: run 1s;
    animation: run 1s linear, shake infinite 2s linear;
    display: flex;
    text-align: center;
    justify-content: center;
  }
}
